    //         this.getWxInfo(allData[""], (d) => {
    //           this.middleAttention = d;
    //         });

    //         this.otherAttention = allData[""];

    //         this.concatHandle();
    //       }
    //     })
    //     .catch((err) => { });
    // },
    getWxInfo (data, func) {
      let keys = Object.keys(data);
      let values = Object.values(data);
      values.forEach((v) => {
        let wx = v[""];
        if (typeof wx !== "string") {
          let arr = [];
          for (let j in wx) {
            let child = wx[j].split(",");
            let cArr = [];
            for (let k in child) {
              if (child[k] !== "None") {
                cArr.push({
                  name: child[k],
                  abst: "",
                });
              }
            }
            child = cArr;
            arr.push(...child);
          }
          v[""] = arr;
        } else {
          let child = wx.split(",");
          let cArr = [];
          for (let i in child) {
            if (child[i] !== "None") {
              cArr.push({
                name: child[i],
                abst: "",
              });
            }
          }
          child = cArr;
          v[""] = child;
        }
        func && func(data);
      });
    },

    handleShowDialog (txt, index, i, sdata, $event, type) {
      if (txt === "-") {
        this.showDialog1 = false;
        return;
      }

      let abst = "";
      if (sdata === "moreData") {
        abst = this[sdata][i]["abst"];
      } else {
        abst = this[sdata][index][""][i]["abst"];
      }

      if (!abst && type != "variantPmid" && sdata === "moreData") {
        this.$get(this.$Url.query.genebyOmId, {
          omim_id: txt,
        })
          .then((res) => {
            if (res.status === 200) {
              let data = res.data;
              if (data) {
                this.dialogInfo = {
                  title: txt,
                  description:
                    data.gene_summary !== "" ? data.gene_summary : "",
                };
                if (sdata === "moreData") {
                  this.$set(
                    this[sdata][i],
                    "abst",
                    this.dialogInfo.description
                  );
                } else {
                  this.$set(
                    this[sdata][index][""][i],
                    "abst",
                    this.dialogInfo.description
                  );
                }

                this.showDialog1 = true;
                let width = document.body.clientWidth;
                let height = document.body.clientHeight;
                this.InfoDiaPos = {
                  pageX:
                    $event.pageX + 300 > width
                      ? $event.pageX - 380
                      : $event.pageX,
                  pageY:
                    $event.pageY + 300 > height
                      ? $event.pageY - 300
                      : $event.pageY,
                };
              }
            }
          })
          .catch((err) => { });
      } else if (type == "variantPmid") {
        /**
         * 
         */
        window.$.ajax({
          headers: {
            token: Cookies.get("token"),
          },
          url: this.$Url.query.literature,
          data: {
            literatureId: txt,
          },
          success: (res) => {
            console.log(res);
            this.dialogInfo = {
              title: txt,
              description: res.data.literatureData,
              type: "PMID:" + txt,
            };
            this.showDialog1 = true;
            let width = document.body.clientWidth;
            let height = document.body.clientHeight;
            this.InfoDiaPos = {
              pageX:
                $event.pageX + 300 > width ? $event.pageX - 380 : $event.pageX,
              pageY:
                $event.pageY + 300 > height ? $event.pageY - 300 : $event.pageY,
            };
          },
        });
      } else {
        this.dialogInfo = {
          title: txt,
          description: abst,
        };
        this.showDialog1 = true;
        let width = document.body.clientWidth;
        let height = document.body.clientHeight;
        this.InfoDiaPos = {
          pageX: $event.pageX + 300 > width ? $event.pageX - 380 : $event.pageX,
          pageY:
            $event.pageY + 300 > height ? $event.pageY - 300 : $event.pageY,
        };
      }
    },
    handleHideDialog (ite) {
      this.showDialog1 = false;
    },
    toPdf () {
      let dom = document.createElement("a");
      let fileName = ".pdf";
      dom.download = fileName;
      dom.style.display = "none";
      dom.href = this.$Url.pdf.shimoshi;
      document.body.appendChild(dom);
      dom.click();
      document.body.removeChild(dom);
    },
    // 
    showMoreDialog (item) {
      this.moreData = item;
      this.moreDialog = true;
    },

    /**
     * 
     */
    toLineHandle (item) {
      let arr = item.split(":");
      let openUrl = `http://genome.ucsc.edu/cgi-bin/hgTracks?db=hg38&lastVirtModeType=default&lastVirtModeExtraState=&virtModeType=default&virtMode=0&nonVirtPosition=&position=chr${arr[0]}%3A${arr[1]}`;
      window.open(openUrl, "_blank");
    },

      linkXGJBHandle(txt, $event){
      window.open('/#/query/disease?search='+ txt +'&active=1', "_blank");
    },

    /**
     * 
     */
    showXGJBHandle (txt, $event) {
      /**
       * 
       */
      window.$.ajax({
        headers: {
          token: Cookies.get("token"),
        },
        url: this.$Url.query.diseaseInformation,
        data: {
          OMIM_id: txt,
        },
        success: (res) => {
          if (res.status === 200) {
            console.log("", res.data);
            let data = res.data;
            this.dialogInfo = {
              title: txt,
              description: data.Description || data.definition_orp || "",
              type: "OMIM:" + txt,
            };
            this.showDialog1 = true;
            let width = document.body.clientWidth;
            let height = document.body.clientHeight;
            this.InfoDiaPos = {
              pageX:
                $event.pageX + 300 > width ? $event.pageX - 380 : $event.pageX,
              pageY:
                $event.pageY + 300 > height ? $event.pageY - 300 : $event.pageY,
            };
          }
        },
        error: (err) => { },
      });
    },

    /**
     * 
     */
    concatHandle () {
      //   

      let highAttentionObj = Object.keys(this.highAttention).map((item) => {
        let highAttentionItem = this.highAttention[item][""].map(
          (fItem) => fItem.name
        );
        return highAttentionItem;
      });

      let middleAttentionObj = Object.keys(this.middleAttention).map((item) => {
        let middleAttentionItem = this.middleAttention[item][""].map(
          (fItem) => fItem.name
        );
        return middleAttentionItem;
      });

      let highAttentionArr = [].concat(...highAttentionObj);
      let middleAttentionArr = [].concat(...middleAttentionObj);
      this.references = highAttentionArr.concat(middleAttentionArr);
      this.references = this.references.filter((item) => item != "-");
      this.searchLiteratureHandle();
    },
    searchLiteratureHandle () {
      if (this.references.length != 0) {
        this.references.forEach((item, index) => {
          if (index < 10) {
            this.viewLiteratureLayerHandle(item);
          }
        });
      }
      console.log(this.searchLiterature);
    },

    viewLiteratureLayerHandle (txt) {
      console.log(txt);
      this.$get(this.$Url.query.literature, {
        literatureId: txt,
      }).then((res) => {
        if (res.status === 200) {
          let resData = res.data;
          let arr = {
            author: `${resData.author}&nbsp;&nbsp;`, //
            title: `${resData.title}&nbsp;&nbsp;`,//
            periodicalName: `${resData.periodicalName}&nbsp;`, //
            periodicalNumber: `${resData.periodicalNumber}&nbsp;`, //
            publishingTime: `${resData.publishingTime}&nbsp;&nbsp;`, //
            summary: `${resData.summary}`, // 
          }
          this.searchLiterature.push(arr);
        }
      });

      // let arr = {
      //     author:'GILLESPIE', //
      //     title:'COVELLI, B. CRYSTALLINE CORNEAL DYSTROPHY. REPORT OF A CASE &nbsp;',//
      //     periodicalName:'American journal of ophthalmology', //
      //     periodicalNumber:'56', //
      //     publishingTime:'465-7 (1963).', //
      //     summary:'CRYSTALLINE CORNEAL DYSTROPHY.', // 
      // }

      // this.searchLiterature.push(arr);
    },

    /**
     * 
     */
    literatureClickHandle (name) {
      // var tempwindow=window.open();
      // tempwindow.location=`https://pubmed.ncbi.nlm.nih.gov/${name}`;
      window.open(`https://pubmed.ncbi.nlm.nih.gov/${name}`)
    }
  },
  mounted () {
    // this.init();
  },
};
</script>

