      width: 160px;
      height: 48px;
      line-height: 48px;
      border-radius: 36px;
      margin-right: 20px;
      text-align: center;
      font-size: 18px;
      color: #fff;
      background-image: $bgGradient2;
      cursor: pointer;
    }
  }
}
.query-details {
  margin-bottom: 40px;
}
.horizontal-bar {
  .tab {
    width: 1128px;
    .tit {
      display: block;
      width: 100%;
      padding: 0;
      // overflow-x: auto;
      p {
        display: flex;
        width: calc(200%);
        padding: 0 35px;
        box-sizing: border-box;
      }
      &.tit-fix {
        position: absolute;
        width: calc(100% + 40px);
        margin-left: -20px;
      }
      &.tit2 {
        background: none;
      }
    }
    .context {
      display: block;
      width: calc(200%);
      border-color: transparent;
      li {
        background-color: transparent;
      }
    }
    .context-fix {
      position: absolute;
      top: 80px;
      width: calc(100% + 40px);
      height: calc(100% - 70px - 12px - 50px);
      border: 1px solid $bgColor5;
      border-top-left-radius: 56px;
      border-bottom-left-radius: 10px;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 56px;
      margin-left: -20px;
      &.no-data {
        width: 100%;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: $bgColor9;
      }
      .no-data {
        height: 40px;
        line-height: 40px;
      }
    }
  }
}
.block4 {
