    text-indent: 25px;
    font-size: 22px;
    color: #000;
  }
}
.more {
  cursor: pointer;
}
.spn {
  font-size: 20px;
}
.references {
  .references-cont {
    display: flex;
    flex-wrap: wrap;
    p {
      padding-bottom: 25px;
      line-height: 40px;
      // text-indent: 25px;
      font-size: 22px;
      color: #333;
      strong {
        display: inline-block;
        padding: 0 6px;
      }
    }
  }
  .description {
    display: flex;
    flex-wrap: wrap;
    span {
      cursor: pointer;
      width: 100px;
      padding: 5px 10px;
      em {
        font-style: normal;
      }
      &:hover {
        color: #3bcaff;
      }
    }
  }
}
